import React,{ useEffect, useState } from 'react'
import { EditorState, convertToRaw,ContentState } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import styles from './styles.module.css'
import axios from '../../axios'
import Button from '../../components/Button'
import Table from '../../components/Table'
import { Draggable } from 'react-beautiful-dnd';
function Review(props) {
    
    // const [linkTemp, setLinkTemp] = useState([]);
    const [showPanelInput, setShowPanelInput] = useState(false);
    const [refreshRender, setRefreshRender] = useState(false);
    const [dataEdit, setDataEdit] = useState({});
    const [poinLength, setPoinLength] = useState(0);
    const [showLoadingProgress, setShowLoadingProgress] = useState(false);
    const [loadingProgress, setLoadingProgress] = useState(0);
    const [poin, setPoin] = useState([]);
    const [poinInput, setPoinInput] = useState([]);
    const [ketInput, setKetInput] = useState([]);
    const [judul, setJudul] = useState("");
    const [inputMode, setInputMode] = useState("");
    const [tutor, setTutor] = useState("");
    const [thumbnailName, setThumbnailName] = useState("");
    const [iconName, setIconName] = useState("");
    const [videoName, setVideoName] = useState("");
    const [review, setReview] = useState([]);
    const [mapel, setMapel] = useState([]);
    const [kelas, setKelas] = useState([]);
    const [babSoal, setBabSoal] = useState([]);
    const [subBabSoal, setsubBabSoal] = useState([]);
    const [fileVideo, setfileVideo] = useState(new FormData());
    // const [tingkatKesulitan, setTingkatKesulitan] = useState([]);
    const [selectMapel, setSelectMapel] = useState("");
    const [selectKelas, setSelectKelas] = useState("");
    const [selectbabSoal, setSelectBabSoal] = useState("");
    const [selectSubBabSoal, setSelectSubBabSoal] = useState("");
    const [linkImg, setLinkImg] = useState("");
    const [img, setImg] = useState(new FormData());
    const [icon, setIcon] = useState(new FormData());

    // const [selectTingkatKesulitan, setSelectTingkatKesulitan] = useState("");
    const base_url = "https://fisikamu.xyz";

    let editorState = EditorState.createEmpty();
  const [description, setDescription] = useState(editorState);
  const onEditorStateChange = (editorState) => {
    //   console.log(htmlToDraft(draftToHtml(convertToRaw(editorState.getCurrentContent()))));
    setDescription(editorState);
  }

  const aturPoin = (e) => {
      if(e.target.value > poinInput.length){
        let i= 0
        while (i < (e.target.value - poinInput.length)) {
            setPoinInput(poinInput => [...poinInput, 0]);
            setKetInput(ketInput => [...ketInput, ""]);
            i+=1;   
        }
      }else{
          setPoinInput([...poinInput.slice(0,e.target.value)]);
          setKetInput([...ketInput.slice(0,e.target.value)]);
      }
    //   5
    //   0,1,2,3,4
    //   2
    //   0,1
    //   =
    //   0,2

    //   5- = 2
    //   3- = 2
    //   let temparr = [...poinInput];
    
    // if(poinInput.length === temp){
    //     // temparr[i]=e.target.value;
    //     // setPoin(temparr);
    //     setPoinInput(temparr);
    // }else{
    // }
    //     let temp = [];
    //     poinInput.map((e,i)=>{
    //         temp[i] =e;
    //     })
    //     // setPoinInput(temp);
    // }
    // setPoin(Array(parseInt(e.target.value)).fill(""));
    // if(poin.length !== 0){
    //         let temp = [];
    //     poin.map((e,i)=>{
    //         temp[i] =e;
    //     })
    //     // setPoin(temp);
    // }
    setPoinLength(e.target.value)
}

    const selectMapelChanged = (e) => {
        setSelectMapel(e.target.value);
    }
    
   

     const selectKelasChanged = (e) => {
        setSelectKelas(e.target.value)
    }
    
    const selectBabSoalChanged = (e) => {
        setSelectBabSoal(e.target.value)
    }
    const ResetFilter = () => {
        setSelectBabSoal("");
        setSelectSubBabSoal("");
    }
    
    const selectSubBabSoalChanged = (e) => {
        setSelectSubBabSoal(e.target.value)
    }
    
  

    const onImageChange = (event) => {
        let files = event.target.files;
        const extLength = files[0]["name"].split(".").length;
        const dateCode = Date.now();
        const ext = files[0]["name"].split(".")[extLength - 1];
        const file_name = "thumbnail_"+dateCode+"."+ext;
        setThumbnailName(file_name);
        if (FileReader && files && files.length) {
           
            img.append('name', "thumbnail_"+dateCode)
            img.append('name_before', "thumbnail_"+dateCode)
            img.append('file', files[0])
          
             
        }
                    
    }
    const onIconChange = (event) => {
        let files = event.target.files;
        const extLength = files[0]["name"].split(".").length;
        const dateCode = Date.now();
        const ext = files[0]["name"].split(".")[extLength - 1];
        const file_name = "icon_video_"+dateCode+"."+ext;
        setIconName(file_name);
        if (FileReader && files && files.length) {
           
            icon.append('name', "icon_video_"+dateCode)
            icon.append('name_before', "icon_video_"+dateCode)
            icon.append('file', files[0])
          
             
        }
                    
    }

    const publishData = (dataTemp) => {

        const data = {
            id: dataTemp.id,
            judul: dataTemp.judul,
            nomor: dataTemp.nomor,
            tutor: dataTemp.tutor,
            deskripsi: dataTemp.Deskripsi,
            poin: dataTemp.poin,
            thumbnail: dataTemp.thumbnail,
            icon: dataTemp.icon,
            link_video: dataTemp.link_video,
            status:(dataTemp.status == "publish"?"no publish":"publish"),
            id_sub_bab: dataTemp.id_sub_bab,
            // id_bab: selectbabSoal,
           
        }

        // console.log(data);
        axios.put('/video_materi', data)
        .then(function (response) {
            console.log(response);
            if (response.status == 200) {
                // setPanelChange("main");
                setReview([]);
                getReview();
                // setPanelChange("isi soal");
                // setHasilSearchSoal([]);
                setRefreshRender(true);
            }
        });
    }

    const editData = (dataTemp) => {
        setShowPanelInput(showPanelInput => !showPanelInput);
        setInputMode("edit");
        setJudul(dataTemp.judul);
        setTutor(dataTemp.tutor);
        console.log(dataTemp);
        const blocksFromHtml = htmlToDraft(dataTemp.Deskripsi);
        const { contentBlocks, entityMap } = blocksFromHtml;
        const contentState = ContentState.createFromBlockArray(contentBlocks, entityMap);
        const editorState = EditorState.createWithContent(contentState);
        setDescription(editorState);
        const poinTemp = dataTemp.poin.split(",");
        // console.log(a);
        let temparr = Array(poinTemp.length).fill(0);
        poinTemp.map((e,i)=>{
            temparr[i]=e;
        });
        // console.log(temparr);
        setPoinLength(poinTemp.length);
        setPoinInput(temparr);
        setPoin(temparr);
        setSelectBabSoal(dataTemp.id_bab);
        setSelectSubBabSoal(dataTemp.id_sub_bab);
        // setThumbnailName()
        
        // setPoinInput(Array(poinTemp.length).fill(0));
        // setPoin(Array(poinTemp.length).fill(0));
        // console.log(JSON.parse(dataTemp.poin));
        let poin_ket = [];
        poinInput.map((e,i)=>{
            poin_ket.push(
                JSON.stringify({
                    'keterangan': ketInput[i],
                    'poin' : e
                })
            )
        })
        setDataEdit(
            {
                judul: judul,
                nomor: dataTemp.nomor,
                tutor: tutor,
                deskripsi: draftToHtml(convertToRaw(description.getCurrentContent())),
                poin: poin_ket.toString(),
                thumbnail: dataTemp.thumbnail,
                link_video: dataTemp.link_video,
                id_sub_bab: selectSubBabSoal,
                // id_bab: selectbabSoal,
               
            }
        )
    }

    const hapusData = (dataTemp) => {
        let  data_delete = require('querystring');
        data_delete = data_delete.stringify({
            id: dataTemp.id
        });
    
    
        axios.delete('/video_materi', {
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            data : data_delete
        }) .then(function (response) {
                    console.log(response);
                    if(response.status === 201){
                        // setPanelChange("main");
                        setRefreshRender(true);
                        setRefreshRender(true);
                        setRefreshRender(true);
                    }
                });
       
    }

    const handleOnDragEnd = (result) => {
        let arr = review;
         const old_index = result.source.index;
         const new_index = result.destination.index;
         
         if (new_index >= arr.length) {
            let k = new_index - arr.length + 1;
            while (k--) {
                arr.push(undefined);
            }
        }
         arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
        //  console.log("arr");
        //  console.log(arr);
         let dataTemp = [];
         for (let i = 0; i < arr.length; i++) {
            const childData = {
                id: arr[i].id,
                nomor: i+1,
            }
             dataTemp.push(childData);
             
         }
        
         const data = {
                data_nomor: dataTemp,
         }
        //  console.log(data);
        //  console.log(review);
         
         axios.put('/video_materi', data)
                .then(function (response) {
                    // console.log(response);
                    if (response.status == 200) {
                        // setPanelChange("main");
                        setReview([]);
                        getReview();
                        // setPanelChange("isi soal");
                        // setHasilSearchSoal([]);
                        setRefreshRender(true);
                    }
                });
    }
    
    // const selectTingkatKesulitanChanged = (e) => {
    //     setSelectTingkatKesulitan(e.target.value)
    // }

    const uploadVideo = (event)=>{
        let files = event.target.files;
        //  console.log(files[0]["name"]);
        //  console.log((Date.now()));
        const extLength = files[0]["name"].split(".").length;
        const dateCode = Date.now();
        const ext = files[0]["name"].split(".")[extLength - 1];
        const file_name = "video_materi_"+selectbabSoal+"_"+selectSubBabSoal+"_"+dateCode+"."+ext;
        setVideoName(file_name);
        if (FileReader && files && files.length) {
           
            fileVideo.append('name', "video_materi_"+selectbabSoal+"_"+selectSubBabSoal+"_"+dateCode)
            fileVideo.append('name_before', "video_materi_"+selectbabSoal+"_"+selectSubBabSoal+"_"+dateCode)
            fileVideo.append('file', files[0])
          
             
        }
        
    }

    const setIsiPoin = (e,i) => {
        // console.log(e.target.value);
        // console.log(i);
        let temparr = [...poinInput];
        temparr[i]=e.target.value;
        // setPoin(temparr);
        setPoinInput(temparr);

    }
    const setIsiKet = (e,i) => {
        // console.log(e.target.value);
        // console.log(i);
        let temparr = [...ketInput];
        temparr[i]=e.target.value;
        // setPoin(temparr);
        setKetInput(temparr);

    }

    const save = () => {

        if(inputMode ===  "edit"){
            if (judul !== ""
                && tutor !== ""
                && draftToHtml(convertToRaw(description.getCurrentContent())) !== ""
                && selectSubBabSoal !== ""
                // && selectbabSoal !== ""
            ) {
              
                // console.log(data);
               

                axios.put('/video_materi', dataEdit)
                    .then(function (response) {
                        console.log(response);
                        setRefreshRender(true);
                        setShowPanelInput(showPanelInput => !showPanelInput);
                        setRefreshRender(false);
                    });
                
                // axios.post('/file_upload', fileVideo)
                //     .then(function (response) {
                //         console.log(response);
                //     });
                //     axios.post('/file_upload', img)
                //     .then(function (response) {
                //         console.log(response);
                //     });
              
            } else {
                alert("Isi Semua Data");
           }
        }else{
            if (fileVideo !== null
                && judul !== ""
                && tutor !== ""
                && draftToHtml(convertToRaw(description.getCurrentContent())) !== ""
                && selectSubBabSoal !== ""
                // && selectbabSoal !== ""
            ) {
                setShowLoadingProgress(true);
                const link_video ="/uploads/" + videoName;
                const link_img ="/uploads/" + thumbnailName;
                const link_icon ="/uploads/" + iconName;
                let poin_ket = [];
                poinInput.map((e,i)=>{
                    poin_ket.push(
                        JSON.stringify({
                            'keterangan': ketInput[i],
                            'poin' : e
                        })
                    )
                })
                 const data = {
                    judul: judul,
                    nomor: review.length+1,
                    tutor: tutor,
                    deskripsi: draftToHtml(convertToRaw(description.getCurrentContent())),
                    poin: poin_ket.toString(),
                    thumbnail: link_img,
                    icon: link_icon,
                    link_video: link_video,
                    id_sub_bab: selectSubBabSoal,
                    // id_bab: selectbabSoal,
                   
                }
                // console.log(data);
                axios.post('/video_materi', data)
                    .then(function (response) {
                        console.log(response);
                        setRefreshRender(true);
                        setRefreshRender(false);
                    });
                
                
                const config ={
                    onUploadProgress:function(progressEvent) {
                        let percentCompleted  = Math.round( (progressEvent.loaded * 100) / progressEvent.total );
                        // console.log(percentCompleted);
                        if(percentCompleted<100){
                            setLoadingProgress(percentCompleted);   
                        }
                        if(percentCompleted === 100){
                            setShowPanelInput(false);
                            setShowLoadingProgress(false);

                        }
                    }
                }    
                axios.post('/file_upload', fileVideo,config)
                    .then(function (response) {
                        console.log(response);
                    });
                    axios.post('/file_upload', img)
                    .then(function (response) {
                        console.log(response);
                    });
                    axios.post('/file_upload', icon)
                    .then(function (response) {
                        console.log(response);
                    });
              
            } else {
                alert("Isi Semua Data");
           }
        }
        
    }

    const panelInputReview =
        <div className={styles.panel_input_review}>
            <div className={styles.panel_input_review_layer_inside}>
                <h1>Buat Video Materi</h1>
                {/* <p>{description == null?"":description}</p> */}
                {showLoadingProgress?
                <div  className={styles.modal}>
                
                <div  className={styles.modal_content}>
                    <h2>{loadingProgress}% / 100%</h2>
                </div>

        </div>:
        <div>
            
        </div>
            }
                
                <input type="text" placeholder="Judul Video" onChange={(e) => { setJudul(e.target.value) }} value={judul} />
                <br/>
                
                <input type="text" placeholder="Tutor" onChange={(e) => { setTutor(e.target.value) }} value={tutor} />
                <br/>
                <br/>
            
            <h5>Deskripsi :</h5>
            <div className={styles.editor_style}>
            <Editor
                editorState={description}
                onEditorStateChange={onEditorStateChange}
              />
            </div>
            <textarea style={{display:'none'}} disabled  value={draftToHtml(convertToRaw(description.getCurrentContent())) } />
            <span>Poin : </span> <input type="number" min={0} onChange={aturPoin} value={poinLength}/>
            <br/>
            {/* {poin.map((e,i)=>(
                <div key={i}>
                    <div>
                    <span>{e}</span>
                    </div>
                </div>
            ))} */}
            <br/>
         

            {poinInput.map((e,i)=>(
                <div key={i}>
                    <span>Poin {i+1} : </span><input type="text" onChange={(e)=>{setIsiPoin(e,i)}} value={e} />
                    <span>ket :  </span><input type="text" onChange={(e)=>{setIsiKet(e,i)}} value={ketInput[i]} />
                </div>
            ))}
            
            {/* <Button link="" title="cek isi text" onClick={() => { console.log(draftToHtml(convertToRaw(description.getCurrentContent()))) }} /> */}
               
                {/* <span>mata pelajaran </span>
            <select value={selectMapel} onChange={selectMapelChanged}>   
            <option value="" disabled ></option>
                {mapel.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
            
            <br/>
            <span>kelas </span>
            <select value={selectKelas} onChange={selectKelasChanged}>
                <option value="" disabled ></option>
                {kelas.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
             */}
            <br/>
            <span>Bab </span>
            <select value={selectbabSoal} onChange={selectBabSoalChanged}>
                <option value="" disabled ></option>
                {babSoal.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
            
            <br/>
            <span>Sub Bab </span>
            <select value={selectSubBabSoal} onChange={selectSubBabSoalChanged}>
                <option value="" disabled ></option>
                {subBabSoal.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
            
            <br/>
            <br/>
            <span>Icon : </span>
            <input type="file" onChange={onIconChange.bind(this)} className="filetype" />
                <br/>
                <br />
            <span>Thumbnail : </span>
            <input type="file" onChange={onImageChange.bind(this)} className="filetype" />
                <br/>
                <br />
                <span>Upload Video : </span>
                <input type="file" onChange={uploadVideo.bind(this)} />
                <br/>
                <br />
            {/* <span>tingkat kesulitan </span>
            <select value={selectTingkatKesulitan} onChange={selectTingkatKesulitanChanged}>
                <option value="" disabled ></option>
                {tingkatKesulitan.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select> */}
            
            <br/>
           
            <br />
                <Button link="" title="cancel" onClick={() => { setShowPanelInput(showPanelInput => !showPanelInput) }} />
                <Button link="" title="save" onClick={() => save()} />
            </div>
        </div>
    
    const mainPanel =
        <>
            <h1>Video Materi</h1>
          
            <Button link="" title="Tambah Video Materi" onClick={() => { setShowPanelInput(showPanelInput => !showPanelInput) }} />
            <h3>Filter</h3>
            <span>Bab </span>
            <select value={selectbabSoal} onChange={selectBabSoalChanged}>
                <option value="" disabled ></option>
                {babSoal.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
            <br/>
            
            <span>Sub Bab </span>
            <select value={selectSubBabSoal} onChange={selectSubBabSoalChanged}>
                <option value="" disabled ></option>
                {subBabSoal.map(item => (
                   <option key={item.id} value={item.id}>{item.deskripsi}</option>
                ))}
            </select>
            <br/>
            <Button link="" title="Terapkan" onClick={()=>getReviewFilter()} />
            <Button link="" title="Reset" onClick={()=>ResetFilter()} />
            <Table
                dropID = {"drop-1"}
                handleOnDragEnd={handleOnDragEnd}
                thList={["no","tutor","deskripsi", "judul", "Sub Bab","Thumbnail", "Video","Status",""]}
                tdList={review.map((item, index) => (
                    <Draggable key={item.id} draggableId={item.id} index={index}>
                        {(provided, snapshot) => (
                            <tr
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                            >
                                <td>{item.nomor}</td>
                                <td>{item.tutor}</td>
                                <td>{item.Deskripsi.length < 70? item.Deskripsi: item.Deskripsi.substring(0,70)+". . ."} </td>
                                <td>{item.judul}</td>
                                <td>{item.sub_bab}</td>
                            <td><a href={base_url+item.thumbnail}>Lihat</a></td>
                            <td><a href={base_url+item.link_video}>Lihat</a></td>
                                <td>{item.status}</td>
                            <td><a href="#0" onClick={()=>editData(item)}>Edit</a>||<a href="#0" onClick={()=>hapusData(item)}>hapus</a>||<a href="#0" onClick={()=>publishData(item)}>{item.status == "publish"?"no publish":"publish"}</a></td>
                            {/* <td><a href="#0" onClick={()=>hapusData(item)}>hapus</a>||<a href="#0" onClick={()=>publishData(item)}>{item.status == "publish"?"no publish":"publish"}</a></td> */}
                            </tr>
                    )}
                    </Draggable>
                ))}
            />
            {/* <Table
                dropID = {"drop_1"}
                thList={["no","tutor","deskripsi", "judul", "Video"]}
                tdList={review.map(item => (
                    <tr key={item.id}>
                        <td>{item.id}</td>
                        <td>{item.tutor}</td>
                        <td>{item.Deskripsi} . . .</td>
                        <td>{item.judul}</td>
                            <td><a href={item.link_video}>{item.link_video}</a></td>
                    </tr>
                    ))}
            /> */}
            
        </>
    

    const getReview = () => {
        axios.get('/video_materi')
            .then(function (response) {
                setReview(response.data);
            });
    }
  
    const getReviewFilter = () => {
       if(selectSubBabSoal !== ""){
        axios.get('/video_materi?id_sub_bab='+selectSubBabSoal)
        .then(function (response) {
            setReview(response.data);
        });
       }
    }


    useEffect(() => {
        async function fetchdata() {
            
            
            getReview();

            axios.get('/bab_soal?id_mapel=1')
                .then(function (response) {
                    setBabSoal(response.data);
            });
          
            // axios.get('/mapel')
            //     .then(function (response) {
            //         setMapel(response.data);
            //     });
            
            // axios.get('/kelas')
            //     .then(function (response) {
            //         setKelas(response.data);
            //     });
            
            // axios.get('/tingkat_kesulitan')
            //     .then(function (response) {
            //         setTingkatKesulitan(response.data);
            //     });
        }
      
        fetchdata();
        //   if (selectMapel !== "") {
        //     axios.get('/bab_soal?id_mapel=' + selectMapel)
        //         .then(function (response) {
        //             setBabSoal(response.data);
        //     });
        // }
      
          if (selectbabSoal !== "") {
            axios.get('/subBab_soal?id_bab=' + selectbabSoal)
                .then(function (response) {
                    setsubBabSoal(response.data);
            });
        }

        if (refreshRender) {
            getReview();
            setRefreshRender(false);
        }
       
    }, [selectbabSoal,refreshRender]);

    return (
        <div className={styles.container}>
            {showPanelInput ? panelInputReview : mainPanel}
        </div>
    )
}

export default Review
